import React from 'react'
import { Link } from 'react-router-dom'

const Home = ()=>{
    return (
        <div>
            <div class="jumbotron text-right text-light mainpage" style={{padding:"10%"}}>
                <div className="overlay"></div>
                <div className="inner">
  <h1 class="display-4">Welcome To Sports Sim Stream!</h1>
  <p>SportsSimStream, LLC is the official home for simulated sports video game club events!</p> 

<p>Club members pay a monthly fee for exclusive access to every tournament and a chance to win cash prizes!</p>
<Link class="btn btn-danger btn-lg" to="/rules" role="button">Learn more!</Link>
</div>
</div>
<div className="container" style={{padding:"20px"}}>
    <div className="row" style={{padding:"20px"}}>
    <div className="col-md-4 iframe-container">
<iframe src="https://player.twitch.tv/?channel=sportssimstream" allowfullscreen="true" height="300" width="480"></iframe>
</div>
<div className="col-sn-2 col-md-5 offset-md-3 text-center" style={{lineHeight:"2.3", padding:"15px"}}>
    <h2>SportsSimStream, LLC is the official home for simulated sports video game club events!</h2> 

<p>Club members pay a monthly fee for exclusive access to every tournament and a chance to win cash prizes!</p>
<Link className="btn btn-danger" style={{width:"100%"}} to="/signup">View Packages</Link>

</div>
</div>
</div>
        </div>
    )
}

export default Home;