import React from 'react'
import sss from '../../assets/images/SSS-4.png'
import { Link } from 'react-router-dom'

export default function Navbar() {
    return (
        <div>
            <nav class="navbar navbar-expand-lg navbar-light bg-light">
  <Link class="navbar-brand" to="/"><img src={sss} width={50} height={50}/></Link>
  <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span class="navbar-toggler-icon"></span>
  </button>

  <div class="collapse navbar-collapse" id="navbarSupportedContent">
    <ul class="navbar-nav mr-auto">
      <li class="nav-item dropdown">
        <a class="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          Sports
        </a>
        <div class="dropdown-menu" aria-labelledby="navbarDropdown">
          <a class="dropdown-item" href="#">NBA 2K</a>
          <a class="dropdown-item" href="#">FIFA</a>
          <a class="dropdown-item" href="#">Madden</a>
          <a class="dropdown-item" href="#">WWE 2K</a>
          <a class="dropdown-item" href="#">NHL</a>
          <a class="dropdown-item" href="#">MLB The Show</a>
          <a class="dropdown-item" href="#">PES</a>
        </div>
      </li>
      <li class="nav-item dropdown">
        <a class="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          Events
        </a>
        <div class="dropdown-menu" aria-labelledby="navbarDropdown">
          <a class="dropdown-item" href="#">Enter May Events</a>
          <a class="dropdown-item" href="#">Schedules</a>
        </div>
      </li>
      <li class="nav-item">
        <Link class="nav-link" to="/settings">Game Settings</Link>
      </li>
      <li class="nav-item">
        <Link class="nav-link" to="/rules">Game Rules</Link>
      </li>
      <li class="nav-item">
        <Link class="nav-link" to="/history" tabindex="-1" aria-disabled="true">My History</Link>
      </li>
      <li class="nav-item">
        <Link class="nav-link" to="/about">About Us</Link>
      </li>
    </ul>
    <div class="form-inline my-2 my-lg-0">
      <Link class="btn btn-danger my-2 my-sm-0" to="/signup">Sign Up</Link>
    </div>
  </div>
</nav>
        </div>
    )
}
